"use client";

/**
 * New Campaign Page
 *
 * Create a new email marketing campaign using the campaign builder wizard.
 */

import { useRouter } from "next/navigation";
import { useCallback, useState, useEffect } from "react";
import { ArrowLeft } from "lucide-react";
import Link from "next/link";
import { Button } from "@stratos/ui";
import { CampaignBuilder } from "@/components/marketing/campaign-builder";
import { useCampaignStore } from "@/lib/stores/campaign-store";
import type { CampaignDraft } from "@/types/campaigns";

export default function NewCampaignPage() {
	const router = useRouter();
	const [isSubmitting, setIsSubmitting] = useState(false);
	const { openBuilder, closeBuilder, addCampaign } = useCampaignStore();

	// Initialize builder when page loads
	useEffect(() => {
		openBuilder();
		return () => closeBuilder();
	}, [openBuilder, closeBuilder]);

	const handleSave = useCallback(
		async (draft: CampaignDraft) => {
			setIsSubmitting(true);
			try {
				// TODO: Replace with actual API call
				await new Promise((resolve) => setTimeout(resolve, 1000));

				// Create campaign in store (mock)
				const newCampaign = {
					id: `campaign_${Date.now()}`,
					...draft,
					status: "draft" as const,
					totalRecipients: 0,
					sentCount: 0,
					deliveredCount: 0,
					openedCount: 0,
					uniqueOpens: 0,
					clickedCount: 0,
					uniqueClicks: 0,
					bouncedCount: 0,
					complainedCount: 0,
					unsubscribedCount: 0,
					failedCount: 0,
					revenueAttributed: 0,
					conversionsCount: 0,
					createdAt: new Date().toISOString(),
					updatedAt: new Date().toISOString(),
				};

				addCampaign(newCampaign);
				router.push(`/dashboard/marketing/campaigns/${newCampaign.id}`);
			} catch (error) {
				console.error("Failed to save campaign:", error);
			} finally {
				setIsSubmitting(false);
			}
		},
		[addCampaign, router]
	);

	const handleSend = useCallback(
		async (draft: CampaignDraft) => {
			setIsSubmitting(true);
			try {
				// TODO: Replace with actual API call
				await new Promise((resolve) => setTimeout(resolve, 1500));

				// Create and send campaign (mock)
				const newCampaign = {
					id: `campaign_${Date.now()}`,
					...draft,
					status: "sending" as const,
					totalRecipients: 2156, // Would come from server
					sentCount: 0,
					deliveredCount: 0,
					openedCount: 0,
					uniqueOpens: 0,
					clickedCount: 0,
					uniqueClicks: 0,
					bouncedCount: 0,
					complainedCount: 0,
					unsubscribedCount: 0,
					failedCount: 0,
					revenueAttributed: 0,
					conversionsCount: 0,
					sendingStartedAt: new Date().toISOString(),
					createdAt: new Date().toISOString(),
					updatedAt: new Date().toISOString(),
				};

				addCampaign(newCampaign);
				router.push(`/dashboard/marketing/campaigns/${newCampaign.id}`);
			} catch (error) {
				console.error("Failed to send campaign:", error);
			} finally {
				setIsSubmitting(false);
			}
		},
		[addCampaign, router]
	);

	const handleSchedule = useCallback(
		async (draft: CampaignDraft, scheduledFor: string) => {
			setIsSubmitting(true);
			try {
				// TODO: Replace with actual API call
				await new Promise((resolve) => setTimeout(resolve, 1000));

				// Schedule campaign (mock)
				const newCampaign = {
					id: `campaign_${Date.now()}`,
					...draft,
					status: "scheduled" as const,
					scheduledFor,
					totalRecipients: 2156, // Would come from server
					sentCount: 0,
					deliveredCount: 0,
					openedCount: 0,
					uniqueOpens: 0,
					clickedCount: 0,
					uniqueClicks: 0,
					bouncedCount: 0,
					complainedCount: 0,
					unsubscribedCount: 0,
					failedCount: 0,
					revenueAttributed: 0,
					conversionsCount: 0,
					createdAt: new Date().toISOString(),
					updatedAt: new Date().toISOString(),
				};

				addCampaign(newCampaign);
				router.push(`/dashboard/marketing/campaigns/${newCampaign.id}`);
			} catch (error) {
				console.error("Failed to schedule campaign:", error);
			} finally {
				setIsSubmitting(false);
			}
		},
		[addCampaign, router]
	);

	const handleCancel = useCallback(() => {
		router.push("/dashboard/marketing/campaigns");
	}, [router]);

	return (
		<div className="flex h-full flex-col">
			{/* Header */}
			<div className="flex items-center gap-4 border-b px-6 py-4">
				<Button variant="ghost" size="icon" asChild>
					<Link href="/dashboard/marketing/campaigns">
						<ArrowLeft className="size-4" />
					</Link>
				</Button>
				<div>
					<h1 className="text-lg font-semibold">Create Campaign</h1>
					<p className="text-sm text-muted-foreground">
						Build a new email marketing campaign
					</p>
				</div>
			</div>

			{/* Campaign Builder */}
			<div className="flex-1 overflow-hidden">
				<CampaignBuilder
					onSave={handleSave}
					onSend={handleSend}
					onSchedule={handleSchedule}
					onCancel={handleCancel}
					isSubmitting={isSubmitting}
				/>
			</div>
		</div>
	);
}
